import React, { Component } from "react";

//Demonstracja cyklu ycia komponentu

//UWAGA: Dla zobaczenia wyniku dziaania console.log trzeba 
//uy Chroma i wczy w nim "Narzedzia dla developerw" (Ctrl+Shift+I)

interface Props {
};

interface State {
    counter: number;
};

class MyComponentLife extends Component<Props, State>
{
    constructor(props: Props) {
        super(props);

        this.state = {
            counter: 0
        }

        this.increase = this.increase.bind(this);
        
        // etap wygenerowania stanu pocztkowego komponentu
        
    }

    increase() {                            
        this.setState((prevState: State) => ({
            counter: prevState.counter + 1
        }));        
    }

    static getDerivedStateFromProps(props: Props, state: State) {
        
        // Aktualizacja stanu na skutek zmiany props
        
        console.log("getDerivedStateFromProps - sprawdzenie props");

        return null;
    }
    
    getSnapshotBeforeUpdate(prevProps: Props, prevState: State) {
        
        // Wykorzystywana jest do wykonania operacji przed aktualizacj komponentu

        console.log("getSnapshotBeforeUpdate - PRZED AKTUALIZACJ");

        return null;        
    }

  

    componentDidMount(): void {
        
        // Wywoywana, gdy komponent zosta utworzony, czyli po pierwszym wywoaniu
        // render(). Suy do wywoywania operacji inicjujcych, np. za pomoc HTTP

        console.log("componentDidMount - MONTOWANIE");
        
        
    }

    componentDidUpdate(prevProps: Props, prevState: State): void {
        
        // Przykadowe uycie  wykonywanie akcji na podstawie informacji, czy zmienione
        // zostay props (waciwoci), np. wysanie ponownego dania HTTP, jeli wykryto,
        // i komponent otrzyma now warto props
        //Wykonywane po aktualizacji komponentu

        
        console.log("componentDidUpdate - PO AKTUALIZACJI");
        
    }

    componentWillUnmount(): void {
        
        // Metoda uywana w momencie usuwania komponentu lub na pocztku przy odwieaniu

        console.log("componentWillUnmount - USUWAM KOMPONENT");

    }

    shouldComponentUpdate(nextProps: Props, nextState: State) {
        
        // Zwraca true lub false, decyduje o tym, czy komponent ma ponownie zosta
        // wyrenderowany (wywietlony). Dziki niej moemy np. z powodw wydajnociowych
        // zablokowa ponowne renderowane. Uruchamiana jest w momencie uzyskania nowych
        // props lub zmiany stanu

        //Tutaj dla przykadu, waczamy aktualizacj widoku tylko wtedy, gdy warto licznika przed zmian jest liczb nieparzyst
        //Zatem pokazywane s tylko liczby parzyste (po zmianie)
        
        
        if (this.state.counter % 2 == 0) {
            console.log("   shouldComponentUpdate - BRAK AKTUALIZACJI");
            return false;
        }
        else {

            console.log("   shouldComponentUpdate - Zlecenie AKTUALIZACJI");
            return true;
        }
    }

    //Wywietlenie lub aktualizacja widoku komponentu
    render() {
        return (
            <div>
                <h3>Stan licznika: {this.state.counter}</h3>
                <button onClick={this.increase}>Zwiksz o jeden</button>
            </div>
        );
    }

}

export default MyComponentLife;
